<template>
  <div class="repeating-timer">
    <div class="clock-container">
      <clock :initialClockTime="intervals[currentIntervalIndex].intervalTime" v-on:clock-alarm="onClockEnd();" />
    </div>
    <div class="menu">
      <div class="interval-list">
        <div class="interval" :class="{'active': isActiveInterval(index)}" v-for="(item, index) in intervals" :key="item.label">
          <!-- find the active interval with currentIntervalIndex -->
          {{ item.index }}
          <div class="interval-label">{{ item.label }}</div>
          <div class="interval-time">{{ readableTime(item.intervalTime) }}</div>
        </div>
      </div>
      <div class="spacer"></div>
      <h4>Alarm Sound Selection</h4>
      <div class="sound-list">
        <div class="sound-item" :class="{'active': isActiveSound(item.name)}" v-for="item in alarmSounds" :key="item.name" @click="selectSound(item.name)">
          <span class="sound-name">{{ item.name }}</span>
          <span class="sound-length">{{ readableTime(item.alarmLength) }}</span>
        </div>
      </div>
      <div class="repeat-option">
        <input id="repeat-option" type="checkbox" :checked="repeatForever" />
        <label for="repeat-option">Repeat Forever</label>
      </div>
    </div>
  </div>
</template>

<script>
import Clock from "@/components/Clock";

export default {
  name: 'RepeatingTimer',
  props: {
    repeatForever: {
      default: true,
      type: Boolean
    },
    repeatCount: Number,
  },
  data() {
    return {
      intervals: [
        {
          label: 'Nerd Time',
          intervalTime: 3600,
          playAlarm: true,
        },
        {
          label: 'Exercise Window',
          intervalTime: 600,
          playAlarm: false,
        },
      ],
      alarmSounds: [
          {
            name: 'David Goggins - Lobster Dinner',
            file: 'david-goggins-1-lobsterdinner.mp3',
            alarmLength: 69,
          },
          {
            name: 'David Goggins - Get Out of Your Head',
            file: 'david-goggins-2-getoutofyourhead.mp3',
            alarmLength: 60,
          },
          {
            name: 'David Goggins - Stay Hard',
            file: 'david-goggins-stayhard.mp3',
            alarmLength: 3,
          },
          {
            name: 'Jocko Willink - Good',
            file: 'jocko-good.mp3',
            alarmLength: 139,
          },
      ],
      currentIntervalIndex: 0,
      selectedAlarmSound: null,
    }
  },
  created() {
    //temp setting for selected alarm Sound
    this.selectSound('David Goggins - Stay Hard');
  },
  methods: {
    onClockEnd() {
      //send clock alert and start the next clock interval if it exists
      const currentInterval = this.intervals[this.currentIntervalIndex];
      const selectedAlarmSoundFile = this.selectedAlarmSound.file;
      if(currentInterval.playAlarm){
        //requires a static beginning to the path when importing
        var audio = new Audio(require('../assets/sounds/alerts/' + selectedAlarmSoundFile));
        audio.play();
      }
      if(this.currentIntervalIndex < (this.intervals.length - 1)){
        this.currentIntervalIndex += 1;
      } else if (this.repeatForever) {
        this.currentIntervalIndex = 0;
      }
    },
    readableTime(time){
      //convert seconds into readable time
      const minutes = Math.floor(time / 60);
      let seconds = time % 60;
      if (seconds < 10) seconds = `0${seconds}`;

      return `${minutes}:${seconds}`;
    },
    isActiveInterval(intervalIndex){
      if(intervalIndex == this.currentIntervalIndex){
        return true;
      } else {
        return false;
      }
    },
    isActiveSound(soundName){
      if(soundName == this.selectedAlarmSound.name){
        return true;
      } else {
        return false;
      }
    },
    selectSound(soundName){
      this.selectedAlarmSound = this.alarmSounds.find(sound => sound.name == soundName);
    },
  },
  mounted() {
  },
  components: {Clock},
}
</script>

<style lang="scss" scoped>
  .repeating-timer {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    .clock-container {
      padding: 5%;
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .menu {
      display: flex;
      align-items: center;
      flex-direction: column;
      align-items: flex-start;
      background-color: $background-foreground-color;
      height: 100vh;
      width: 250px;
      h4 {
        font-size: 0.7em;
        padding: 10px 20px;
        margin: 0;
        font-weight: normal;
        text-transform: uppercase;
        font-style: italic;
        color: $text-supplemental-color;
      }
      .interval-list {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        $interval-spacing: 25px;
        .interval {
          padding: $interval-spacing 20px;
          width: 100%;
          font-size: 1em;
          display: flex;
          color: $text-supplemental-color;
          border-bottom: 1px solid $border-dark;
          background-image: linear-gradient(rgba(black, 0), rgba(black, 0.02));
          &.active {
            //background-image: linear-gradient(rgba(red, 0.2), rgba(blue, 0.2));
            font-weight: bold;
            color: $text-highlight-color;
          }
          .interval-label {
            flex-grow: 1;
          }
          .interval-time {
            margin-left: 15px;
            text-align: right;
          }
        }
      }
      .spacer {
        flex-grow: 1;
      }
      .sound-list {
        display: flex;
        align-items: center;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
        padding-bottom: 5px;
        background-color: $background-dark-overlay;
        .sound-item {
          padding: 10px 20px;
          border-top: 1px solid $border-dark;
          font-size: 0.8em;
          color: $text-color;
          cursor: pointer;
          opacity: 0.5;
          .sound-length {
            margin-left: 5px;
            font-size: 0.8em;
            color: $text-supplemental-color;
          }
          &.active {
            opacity: 1;
            font-weight: bold;
          }
        }
      }
      .repeat-option {
        color: $text-supplemental-color;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 20px;
        background-color: $background-dark-overlay;
        border-top: 1px solid $border-dark;
        * {
          opacity: 0.6;
        }
        label {
          user-select: none;
        }
      }
    }
  }
</style>
