<template>
  <div class="clock">{{ readableTime }}</div>
</template>

<script>
  export default {
    name: 'Clock',
    props: {
      initialClockTime: {
        default: 60,
        type: Number
      },
      active: {
        default: true,
        type: Boolean
      }
    },
    data() {
      return {
        clockTime: this.initialClockTime,
        clockInterval: null,
      }
    },
    computed: {
      readableTime(){
        //convert seconds into readable time
        const clockTime = this.clockTime;
        const minutes = Math.floor(clockTime / 60);
        let seconds = clockTime % 60;
        if (seconds < 10) seconds = `0${seconds}`;

        return `${minutes}:${seconds}`;
      },
      timePassed(){
        return this.initialClockTime - this.clockTime;
      }
    },
    methods: {
      startClock() {
        clearInterval(this.clockInterval);
        this.clockInterval = setInterval(() => {
            this.clockTime -= 1;
            if(this.clockTime == 0) {
              this.clockAlarm();
              this.stopClock();
            }
          }, 1000);
      },
      clockAlarm(){
        //sound and animation for clock timeout
        this.$emit('clock-alarm');
      },
      stopClock(){
        clearInterval(this.clockInterval);
      },
      resetClock() {
        this.clockTime = this.initialClockTime;
      },
    },
    watch: { 
      initialClockTime: function() {
        this.clockTime = this.initialClockTime;
        this.startClock();
      }
    },
    mounted() {
      if (this.active) {
        this.startClock();
      }
    }
  }
</script>

<style scoped lang="scss">
  .clock {
    font-weight: bold;
    font-size: 20vh;
  }
</style>